<template>
<div>
<v-tabs
      v-model="tab"
      centered
      icons-and-text
    >
      <v-tabs-slider></v-tabs-slider>

      <v-tab href="#tab-1">
        Zbornik
        <!-- <v-icon>mdi-phone</v-icon> -->
      </v-tab>

      <v-tab href="#tab-2">
        Wywóz
        <!-- <v-icon>mdi-heart</v-icon> -->
      </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item
        value="tab-1"
      >
        <v-card flat>
          <v-card-text>
            <validation-observer
    ref="observer"
    v-slot="{ invalid }"
  >
    <form @submit.prevent="addEditTank">
      <validation-provider
        v-slot="{ errors }"
        name="Numer zbiornika"
        rules="required"
      >
        <v-text-field
          v-model="tankData.name"
          :error-messages="errors"
          label="Numer zbiornika"
          required
        ></v-text-field>
      </validation-provider>
      <validation-provider
        v-slot="{ errors }"
        name="Producent"
        rules="required"
      >
        <v-select
          v-model="tankData.manufacturer"
          :items="tankManufacturerList"
          item-text="name"
          item-value="id"
          :error-messages="errors"
          label="Producent"
          data-vv-name="manufacturer"
          required
        ></v-select>
      </validation-provider>
       <validation-provider
        v-slot="{ errors }"
        name="Pojemność"
        rules="required"
      >
        <v-select
          v-model="tankData.capacity"
          item-text="name"
          item-value="id"
          :items="tankCapacityList"
          :error-messages="errors"
          label="Pojemność"
          data-vv-name="capacity"
          required
        ></v-select>
      </validation-provider>
       <validation-provider
        v-slot="{ errors }"
        name="Właściciel"
        rules="required"
      >
        <v-select
          v-model="tankData.owner"
          item-text="name"
          item-value="id"
          :items="tankOwnerList"
          :error-messages="errors"
          label="Właściciel"
          data-vv-name="owner"
          required
        ></v-select>
      </validation-provider>
      <validation-provider
        v-slot="{ errors }"
        name="Zawór"
        rules="required"
      >
        <v-select
          v-model="tankData.valve"
          item-text="name"
          item-value="id"
          :items="tankValveList"
          :error-messages="errors"
          label="Zawór"
          data-vv-name="valve"
          required
        ></v-select>
      </validation-provider>
      <div class="form-row">
        <div class="form-control">
          <label for="productionYear">Rok produkcji
            <datepicker name="productionYear" format="yyyy" :minimumView="'year'" :maximumView="'year'" v-model="tankData.productionYear"></datepicker>
          </label>
        </div>
        <div class="form-control">
          <v-radio-group v-model="tankData.pin">
            <v-radio
              label="P"
              value="P"
            >P</v-radio>
            <v-radio
              label="N"
              value="N"
            >N</v-radio>
          </v-radio-group>
        </div>
      </div>
    <v-textarea
          v-model="tankData.comment"
          solo
          name="input-7-4"
          label="Uwagi"
        ></v-textarea>
        <v-textarea
          v-model="tankData.workComment"
          solo
          name="input-7-4"
          label="Uwagi do wykonanej pracy"
        ></v-textarea>
      <v-btn
        class="mr-4"
        type="submit"
        :disabled="invalid"
      >
        Zapisz
      </v-btn>
    </form>
  </validation-observer>
          </v-card-text>
        </v-card>
      </v-tab-item>
      <v-tab-item
        value="tab-2"
      >
        <v-card flat>
          <v-card-text>
            <validation-observer
    ref="observer2"
    v-slot="{ invalid }"
  >
    <form @submit.prevent="submit">
      <validation-provider
        v-slot="{ errors }"
        name="Miasto"
        rules="required"
      >
        <v-text-field
          v-model="tankData.city"
          :counter="10"
          :error-messages="errors"
          label="Miasto"
          required
        ></v-text-field>
      </validation-provider>
      <validation-provider
        v-slot="{ errors }"
        name="Ulica"
        rules="required"
      >
        <v-text-field
          v-model="tankData.street"
          :error-messages="errors"
          label="Ulica"
          required
        ></v-text-field>
      </validation-provider>
      <v-btn
        class="mr-4"
        type="submit"
        :disabled="invalid"
      >
        Wywóz
      </v-btn>
    </form>
      </validation-observer>

          </v-card-text>
        </v-card>
      </v-tab-item>
    </v-tabs-items>
</div>
</template>
<script>
import { mapState } from 'vuex';
import { required, min } from 'vee-validate/dist/rules'
import { extend, ValidationObserver, ValidationProvider } from 'vee-validate'
import Datepicker from 'vuejs-datepicker'

extend('required', {
    ...required,
    message: 'Pole {_field_} nie może być puste',
  })

  extend('min', {
    ...min,
    message: 'Minimalna długość znaków to {length}',
  })
// import History from '../../../../../model/History';
// import Api from '../../../../../model/Api';
// import store from '../../../../../store';

export default {
  name: 'TankForm',
  props: ['formData', 'action'],
  components: {
    ValidationProvider,
    ValidationObserver,
    Datepicker,
  },
  data() {
    return {
    //   user: this.$session.get('user_info'),
    tab: null,
      tankData: {
        name: '',
        manufacturer: '',
        capacity: '',
        owner: '',
        valve: '',
        productionYear:new Date(),
        pin: 'P',
      },
      // tankManufacturerList:[],
    };
  },
  created() {
    // const self = this;
    this.$store.dispatch('tank/getManufacturerList');
    this.$store.dispatch('tank/getCapacityList');
    this.$store.dispatch('tank/getOwnerList');
    this.$store.dispatch('tank/getValveList');
    if(this.action === 'edit') {
      this.user_data = this.formData;
    }
  },
  beforeUpdate() {
    // this.$store.dispatch('tank/getManufacturerList');
  },
  methods: {
    addEditTank() {
    //   const self = this;
    this.$store.dispatch('tank/addTank', this.tankData);
      // const data = {
      //   user_first_name: this.user_data.firstName,
      //   user_last_name: this.user_data.lastName,
      //   user_login: this.user_data.login,
      //   user_phone: this.user_data.phone,
      //   user_email: this.user_data.email,
      //   user_password: this.user_data.password,
      //   user_password_repeat: this.user_data.password_repeat,
      // };
      // if (this.action !== 'add') {
      //   data.id = this.formData.id;
      //   // store.dispatch('adminPanel/editUser', data);
      // } else {
        // store.dispatch('adminPanel/addUser', data);
      }
  },
  computed: {
    ...mapState('tank', [
      'tankManufacturerList',
      'tankCapacityList',
      'tankOwnerList',
      'tankValveList',
    ]),
  },
};
</script>

<style lang="scss">
  .v-modal {
    padding: 20px;
  }
  .v-toolbar {
    flex: none;
  }
  // .form-row {
  //   display: flex;
  //   justify-content: space-between;
  // }
  // .form-control {
  //   display: flex;
  //   flex-direction: row;
  //   align-self: stretch;
  // }
</style>
